import { PayloadAction, createSlice } from "@reduxjs/toolkit";

const searhItem = (items: CartItemsTypes[], payload: CartItemsTypes) => {
  return items.find(
    (el) =>
      el.id === payload.id &&
      el.size === payload.size &&
      el.type === payload.type
  );
};

// type ChangeItemsTypes = {
//   size: number;
//   type: string;
//   id: string;
// }

export type CartItemsTypes = {
  imageUrl: string;
  title: string;
  price: number;
  size: number;
  type: string;
  id: string;
  count: number;
};

export interface CartSliceTypes {
  totalPrice: number;
  totalCount: number;
  items: CartItemsTypes[];
}

const initialState: CartSliceTypes = {
  totalPrice: 0,
  totalCount: 0,
  items: [],
};

const cartSlice = createSlice({
  name: "cartSlice",
  initialState,
  reducers: {
    addItem: (state, action: PayloadAction<CartItemsTypes>) => {
      let findItem = searhItem(state.items, action.payload);

      if (findItem) {
        state.items = state.items.map((el) => {
          if (el === findItem) {
            return { ...el, count: el.count + 1 };
          } else {
            return el;
          }
        });
      } else {
        state.items = [...state.items, { ...action.payload, count: 1 }];
      }

      state.totalPrice = state.items.reduce(
        (sum, el) => sum + el.price * el.count,
        0
      );
      state.totalCount = state.items.reduce((sum, el) => sum + el.count, 0);
    },
    minusItem: (state, action: PayloadAction<CartItemsTypes>) => {
      let findItem = searhItem(state.items, action.payload);

      if (findItem && findItem.count === 1) {
        state.items = state.items.filter((el) => el !== findItem);
      } else {
        state.items = state.items.map((el) => {
          if (el === findItem) {
            return { ...el, count: el.count - 1 };
          } else {
            return el;
          }
        });
      }

      state.totalPrice = state.items.reduce(
        (sum, el) => sum + el.price * el.count,
        0
      );
      state.totalCount = state.items.reduce((sum, el) => sum + el.count, 0);
    },
    removeItem: (state, action: PayloadAction<CartItemsTypes>) => {
      let findItem = searhItem(state.items, action.payload);

      state.items = state.items.filter((el) => el !== findItem);

      state.totalPrice = state.items.reduce(
        (sum, el) => sum + el.price * el.count,
        0
      );
      state.totalCount = state.items.reduce((sum, el) => sum + el.count, 0);
    },
    clearItems: (state) => {
      state.items = [];
      state.totalPrice = 0;
      state.totalCount = 0;
    },
  },
});

export const { addItem, clearItems, minusItem, removeItem } = cartSlice.actions; //      ()

export default cartSlice.reducer; //   reducer
