import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";

export interface fetchPizzasProps {
  ascDesc: string;
  sortBy: string;
  search: string;
  activeCategory: number;
}

export const fetchPizzas = createAsyncThunk(
  "pizzaSlice/fetchPizzas",
  async (props: fetchPizzasProps, thunkAPI) => {
    try {
      const { ascDesc, sortBy, search, activeCategory } = props;

      // Fetch URL
      let FETCH_URL;

      if (activeCategory === 0) {
        FETCH_URL = `https://6543640701b5e279de2045aa.mockapi.io/items?sortBy=${sortBy}&order=${ascDesc}&search=${search}`;
      } else {
        FETCH_URL = `https://6543640701b5e279de2045aa.mockapi.io/items?category=${activeCategory}&sortBy=${sortBy}&order=${ascDesc}&search=${search}`;
      }

      let data = await fetch(FETCH_URL);

      let res = await data.json();

      return res as pizzasTypes[];
    } catch (error) {
      return thunkAPI.rejectWithValue(error); //    
    }
  }
);

export type pizzasTypes = {
  id: string;
  imageUrl: string;
  title: string;
  types: number[];
  sizes: number[];
  price: number[][];
  category: number;
  rating: number;
};

interface pizzaSliceTypes {
  pizzas: pizzasTypes[];
  status: string;
}

const initialState: pizzaSliceTypes = {
  pizzas: [],
  status: "",
};

const pizzaSlice = createSlice({
  name: "pizzaSlice",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(fetchPizzas.pending, (state) => {
      // fetchPizzas -     asyncThunk, pending | fulfilled | rejected -    (, , )
      state.status = "loading";
      state.pizzas = [];
    });
    builder.addCase(fetchPizzas.fulfilled, (state, action) => {
      state.status = "success";
      state.pizzas = action.payload; // action.payload -      return res
    });
    builder.addCase(fetchPizzas.rejected, (state) => {
      state.status = "error";
      state.pizzas = [];
    });
  },
});

export default pizzaSlice.reducer; //   reducer
