import { useState } from "react";
import { useDispatch } from "react-redux";
import { addItem } from "../store/cartSlice";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { FC } from "react";
import { RootState } from "../store";

type PizzaBlockProps = {
  id: string;
  imageUrl: string;
  title: string;
  price: number[][];
  sizes: number[];
  types: number[];
};

const PizzaBlock: FC<PizzaBlockProps> = (props) => {
  const { id, imageUrl, title, price, sizes, types } = props;

  const dispatch = useDispatch();
  const typesID = ["", ""];

  const [activeType, setActiveType] = useState(0);
  const [activeSize, setActiveSize] = useState(0);

  const state = useSelector((state: RootState) =>
    state.cart.items.find(
      (el) =>
        el.id === id &&
        el.size === sizes[activeSize] &&
        el.type === typesID[activeType]
    )
  );

  const addCount = () => {
    dispatch(
      addItem({
        imageUrl,
        title,
        price: price[activeType][activeSize],
        size: sizes[activeSize],
        type: typesID[activeType],
        id,
        count: 0
      })
    );
  };

  const checkSize = (index: number) => {
    setActiveSize(index);
  };

  const checkType = (index: number) => {
    setActiveType(index);
  };

  return (
    <div className="pizza-block">
      <Link to={`/pizza/${id}`}>
        <img className="pizza-block__image" src={imageUrl} alt="Pizza" />
      </Link>
      <Link to={`/pizza/${id}`}>
        <h4 className="pizza-block__title">{title}</h4>
      </Link>
      <div className="pizza-block__selector">
        <ul>
          {types.map((type, i) => (
            <li
              key={i}
              onClick={() => checkType(i)}
              className={activeType === i ? "active" : ""}
            >
              {typesID[type]}
            </li>
          ))}
        </ul>
        <ul>
          {sizes.map((size, i) => (
            <li
              key={i}
              onClick={() => checkSize(i)}
              className={activeSize === i ? "active" : ""}
            >
              {size} .
            </li>
          ))}
        </ul>
      </div>
      <div className="pizza-block__bottom">
        <div className="pizza-block__price"> {price[activeType][activeSize]} </div>
        <button
          className="button button--outline button--add"
          onClick={addCount}
        >
          <svg
            width="12"
            height="12"
            viewBox="0 0 12 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
              fill="white"
            />
          </svg>
          <span></span>
          {state && <i>{state.count}</i>}
        </button>
      </div>
    </div>
  );
};

export default PizzaBlock;
