import { createSlice } from "@reduxjs/toolkit";

type sortTypes = {
  name: string;
  sortProperty: string;
};

interface FilterSliceTypes {
  categoryId: number;
  sort: sortTypes;
  search: string;
}

const initialState: FilterSliceTypes = {
  categoryId: 0,
  sort: {
    name: " ",
    sortProperty: "rating",
  },
  search: "",
};

const filterSlice = createSlice({
  name: "filterSlice",
  initialState,
  reducers: {
    setActiveCategory: (state, action) => {
      state.categoryId = action.payload.index;
    },
    setActiveSort: (state, action) => {
      state.sort.name = action.payload.name;
      state.sort.sortProperty = action.payload.property;
    },
    setSearchValue: (state, action) => {
      state.search = action.payload.search;
    },
    setURLfilter: (state, action) => {
      state.categoryId = Number(action.payload.category);
      state.sort.sortProperty = action.payload.sortBy;
      state.sort.name = action.payload.nameSort;
      state.search = action.payload.search;
    },
  },
});

export const {
  setActiveCategory,
  setActiveSort,
  setSearchValue,
  setURLfilter,
} = filterSlice.actions; //      ()

export default filterSlice.reducer; //   reducer
