import { FC, memo } from "react";

const categoriesTypes = [
  "",
  "",
  "",
  "",
  "",
  "",
];

interface CategoriesProps {
  activeCategory: number;
  handlerChangeCategory: (obj: { index: number }) => void;
}

const Categories: FC<CategoriesProps> = memo((props) => {
  const { activeCategory, handlerChangeCategory } = props;

  return (
    <div className="categories">
      <ul>
        {categoriesTypes.map((category, index) => (
          <li
            onClick={() => handlerChangeCategory({ index })}
            key={category}
            className={activeCategory === index ? "active" : ""}
          >
            {category}
          </li>
        ))}
      </ul>
    </div>
  );
});

export default Categories;
