import { useCallback, useEffect, useRef } from "react";
import Categories from "../components/Categories";
import Sort from "../components/Sort";
import PizzaBlock from "../components/PizzaBlock";
import Skeleton from "../components/PizzaBlockSkeleton";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import queryString from "query-string";
import {
  setActiveCategory,
  setActiveSort,
  setURLfilter,
} from "../store/filterSlice";
import {
  fetchPizzas,
  fetchPizzasProps,
  pizzasTypes,
} from "../store/pizzaSlice";
import { sortTypes } from "../components/Sort";
import { FC } from "react";
import { RootState, useAppDispatch } from "../store";

const Home: FC = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const dispatchAsync = useAppDispatch();

  const isMounted = useRef(false);
  const isSearch = useRef(false);

  // State Pizza and Filter
  const statePizza = useSelector((state: RootState) => state.pizza);
  const stateFilter = useSelector((state: RootState) => state.filter);

  // Fetch props
  const ascDesc = stateFilter.sort.sortProperty.includes("-") ? "desc" : "asc";
  const sortBy = stateFilter.sort.sortProperty.replace("-", "");
  const search = stateFilter.search ? stateFilter.search : "";
  const activeCategory = stateFilter.categoryId;

  const handlerChangeCategory = useCallback((obj: { index: number }) => {
    dispatch(setActiveCategory(obj));
  }, []);

  const handlerChangeSort = useCallback(
    (obj: { name: string; property: string }) => {
      dispatch(setActiveSort(obj));
    },
    []
  );

  //    ,   URL    
  useEffect(() => {
    if (location.search) {
      const params = queryString.parse(location.search);
      const nameSort = sortTypes.find((el) => el.sort === params.sortBy)?.name;

      if (
        params.category === String(stateFilter.categoryId) &&
        params.search === stateFilter.search &&
        params.sortBy === stateFilter.sort.sortProperty
      ) {
        isSearch.current = false;
      } else {
        dispatch(setURLfilter({ ...params, nameSort }));
        isSearch.current = true;
      }
    }
  }, []);

  useEffect(() => {
    //       
    if (isMounted.current) {
      const query = queryString.stringify({
        sortBy: sortBy,
        order: ascDesc,
        category: activeCategory,
        search,
      });
      navigate(`?${query}`);
    }

    if (!isSearch.current) {
      dispatchAsync(
        fetchPizzas({
          ascDesc,
          sortBy,
          search,
          activeCategory,
        } as fetchPizzasProps)
      );
    }

    isMounted.current = true;
    isSearch.current = false;
  }, [stateFilter]);

  return (
    <>
      <div className="content__top">
        <Categories
          activeCategory={stateFilter.categoryId}
          handlerChangeCategory={handlerChangeCategory}
        />
        <Sort
          stateSort={stateFilter.sort}
          handlerChangeSort={handlerChangeSort}
        />
      </div>
      <h2 className="content__title"> </h2>
      <div className="content__items">
        {statePizza.status === "loading" && (
          <>
            <Skeleton />
            <Skeleton />
            <Skeleton />
            <Skeleton />
            <Skeleton />
            <Skeleton />
          </>
        )}
        {typeof statePizza.pizzas !== "object" ? (
          <h2>  </h2>
        ) : (
          statePizza.pizzas.map((pizza: pizzasTypes) => (
            <PizzaBlock key={pizza.id} {...pizza} />
          ))
        )}
      </div>
    </>
  );
};

export default Home;
