// import { FC } from "react";
import ContentLoader from "react-content-loader";

const Skeleton = () => (
  <ContentLoader
    speed={1}
    width={280}
    height={490}
    viewBox="0 0 280 490"
    backgroundColor="#bbb9b9"
    foregroundColor="#ecebeb"
  >
    <circle cx="135" cy="130" r="130" />
    <rect x="5" y="265" rx="0" ry="0" width="270" height="30" />
    <rect x="15" y="335" rx="10" ry="10" width="250" height="88" />
    <rect x="15" y="449" rx="0" ry="0" width="90" height="30" />
    <rect x="113" y="445" rx="28" ry="28" width="150" height="45" />
  </ContentLoader>
);

export default Skeleton;
